"use client"

import React, { useState } from 'react'
import { Scanner, type IDetectedBarcode } from '@yudiel/react-qr-scanner'

export default function ScanPage() {
  const [result, setResult] = useState('')

  const handleDecode = (detectedCodes: IDetectedBarcode[]) => {
    if (detectedCodes.length > 0) {
      setResult(detectedCodes[0].rawValue)
      console.log(detectedCodes[0].rawValue)
    }
  }

  const handleError = (error: any) => {
    console.log(error?.message)
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-2xl font-bold text-white mb-6">QR Code Scanner</h1>
        
        <div className="bg-[#1a1b1e] border border-gray-800 rounded-2xl p-6 shadow-xl">
          <div className="aspect-square w-full relative rounded-xl overflow-hidden">
            <Scanner
              onScan={handleDecode}
              onError={handleError}
            />
          </div>
          
          {result && (
            <div className="mt-4 p-4 bg-white/5 rounded-xl">
              <h3 className="text-lg font-semibold text-white mb-2">Scanned Result:</h3>
              <p className="text-gray-300 break-all">{result}</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
} 
